class Main extends React.Component {
  render() {
    return (
      <BrowserRouter>
        {/* <Provider >
          <PersistGate loading={null} > */}
            <header className="pageheader">
              <Header />
            </header>
            <ScrollToTop />
            {/* <App /> */}
            <Routes>
            <Route path="/" element={<App />} className='ppp'/>
      
             <Route path="/aboutus" element={<Aboutus />} />
             <Route path="/nftmember" element={<Nftmember />} />
             <Route path="/Gallery" element={<Gallery />} />
             <Route path="/Login" element={<Login />} />
             <Route path="/ticketholder" element={<Ticketholder />} />
             <Route path="/nft-drop" element={<Nftdrop />} />
             <Route path="/nft-sale" element={<Nftsale />} />
             <Route path="/selectob" element={<SelectOb />} />
             <Route path="/recent-transactions" element={<Recenttransactions />} />
             
            </Routes>
            <Hello />
            <footer>
              <Footer />
            </footer>
          {/* </PersistGate>
        </Provider> */}
      </BrowserRouter>
    );
  }
}
ReactDOM.render(<Main />, document.getElementById("root"));
