import React from 'react';
import { useReactCountdown } from 'use-react-countdown';

export default function App() {
  // Date in format (Month DD, YYYY hh:mm:ss)
  const { days, hours, minutes, seconds } = useReactCountdown(
    'May 28, 2022 00:00:00'
  );

  return (
    <div className='counter-div'>
      <div className='count-holder'>
      <h2>{days}</h2>
      <p>Day</p>
      </div>
      <div className='count-holder'>
      <h2>{hours}</h2>
      <p>Hrs</p>
      </div>
      <div className='count-holder'>
      <h2>{minutes}</h2>
      <p>Min</p>
      </div>
      <div className='count-holder'>
      <h2>{seconds}</h2>
      <p>Sec</p>
      </div>
      {/* <h2>Hours: {hours} </h2>
      <h2>Minutes: {minutes} </h2>
      <h2>Seconds: {seconds} </h2> */}
    </div>
  );
}
