import { Container, Row, Col } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { solid, regular, brands } from '@fortawesome/fontawesome-svg-core/import.macro';
import { Link } from 'react-router-dom';

// ABOUR IMAGE
import mascot from '../../Assets/Mascot.png';
import milestone from '../../Assets/milestone1.png';
import React, { Component, useEffect } from 'react';

// import Footer from '../../Extends/Footer';
function Aboutus() {
  useEffect(() => {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }, [])

    return (
      <div className='about'>
        <div className='about-sec zoomout'>
          <Container>
            <Row>
              <Col sm={8} className='lft-side'>
                <h2 className='sec-head'>About us</h2>

                <p>In years to come, future historians may very well give a one-word answer for wongs rise to the summit.Well, 
                  one acronym, a dash and a number. Covid-19 . But that would only be a scratching the surface of the story. 
                  Unprecedented in scale, unlike any other challenge singapore</p>
                  
                <p>Then came the decision that put wong at the forefront of the covid-19 battle. Then-Health minister gan kim yong nominated 
                  wong as someone he could work well with to be his fellow-co-chair, according to the straits time. Gan discuseed with senior 
                  minister teo chee hean, who took his request to Heng</p>
                
              </Col>
              <Col sm={4} className='ryt-side'>
                <div className='img-holder'>
                  <img src={mascot} alt='' />
                </div>
              </Col>
            </Row>
          </Container>
        </div>

        <div className='milestone-sec'>
          <Container>
            <h2 className='sec-head'>Milestone</h2>
            <div className='img-holder'>
              <img src={milestone} alt='' />
            </div>
          </Container>
        </div>
        {/* <Footer /> */}
      </div>
      
      
    
    );
    
  }
  
  
  export default Aboutus;
  