
import React, { Component, useState } from 'react';
import ReactDOM, { render } from 'react-dom';
import { Link, NavLink } from "react-router-dom";
import { Navbar, Container, Nav, Row, Col, Form, Button} from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { brands, solid, regular } from '@fortawesome/fontawesome-svg-core/import.macro';


// LOGO
import sitelogo from '../Assets/Logo.png';
import ScrollToTop from '../Views/ScrollToTop/Index';




function Header() {
  
  const [expanded, setExpanded] = useState(false);
  
  
  const setExpand = () => {
    setExpanded(false);
    // window.scrollTo(0, 0);
  }
  

    return (
      <div className='main-header'>
      <div className='mar'>
      <Navbar bg='default' expand='lg' expanded={expanded}>
      
      <Container>
        
        <Navbar.Brand as={Link} to={'/'}> 
          <img src={sitelogo} alt='' />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls='basic-navbar-nav' onClick={() => setExpanded(expanded ? false : "expanded")} />
        <Navbar.Collapse id='basic-navbar-nav'>
          
          <Nav className=''>
          {/* <Nav.Link href="/aboutus">ABOUT US</Nav.Link>
          <Nav.Link href="/wallet">NFT MEMBER</Nav.Link>
          <Nav.Link href="/FullpageWrapper">GALLERY</Nav.Link>
          <Nav.Link href="/login">SIGN UP/LOGIN</Nav.Link> */}
            <a href='/aboutus'>ABOUT US</a>
            <a href='/nftmember'>NFT MEMBER</a>
            {/* <NavLink exact='true' as={Link} to={'/Gallery'} onClick={setExpand}>GALLERY</NavLink> */}
            <NavLink exact='true' as={Link} to={'/login'} onClick={setExpand}>SIGN UP/LOGIN</NavLink>
            
          </Nav>
          
          
        </Navbar.Collapse>
        
      </Container>
      

    </Navbar>
    </div>
    </div>
           
    );
  }
  
  export default Header;
  